.home {
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.headerContainer {
    /* width: auto; */
    margin-left: 50px;
    padding-top: 350px;
}

.headerContainer h1 {
    justify-content: center;
    align-items: center;
    /* font-size: 30px; */
    height: 60px;
    /* font-weight: 50; */
    color: #666;
    font-family: 'Nunito', sans-serif;
}

.headerContainer p {
    padding: 0cm;
    /* font-size: 20px; */
    /* font-weight: lighter; */
    color: #666;
    font-family: 'Nunito', sans-serif;
}

.headerContainer button {
    color: #666;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    justify-content: center;
    background-color: #666;
    height: 50px;
    width: 180px;
    color: white;
    text-decoration: none;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    margin-left: 50%;
}


/* .bton button {
    margin-bottom: 500px;
} */

.headerContainer button:hover {
    background-color: #aa8c6e;
    cursor: pointer;
    transition: 0.3s ease-in;
}

@media only screen and (max-width: 650px) {
    .home {
        justify-content: center;
        align-items: center;
    }
    .headerContainer h1,
    .headerContainer p {}
    .headerContainer {
        margin-left: 0px;
        border-radius: 10px;
        padding: 10px;
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: #121619;
        color: white;
    }
    .headerContainer h1 {
        font-size: 40px;
        height: 30px;
        color: white;
        font-family: 'Nunito', sans-serif;
    }
    .headerContainer p {
        font-size: 30px;
        color: #666;
        font-family: 'Nunito', sans-serif;
    }
    .headerContainer button :hover {
        background-color: rgb(225, 225, 225);
        color: #aa8c6e;
        margin-bottom: 500px;
    }
}