  .about {
      width: 100%;
      height: 90vh;
      display: flex;
      flex-direction: column;
  }
  
  .aboutTop {
      width: 100%;
      height: 45%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      font-family: 'Nunito', sans-serif;
  }
  
  .aboutBottom {
      display: flex;
      flex-direction: column;
      height: 55%;
      align-items: center;
      margin-top: 50px;
  }
  
  .aboutBottom p {
      width: 1200px;
      font-family: 'Nunito', sans-serif;
      text-align: center;
      color: #666;
  }
  
  .about .aboutBottom h1 {
      font-weight: 400;
      font-size: 40px;
      color: #666;
      height: 30px;
      display: flex;
      justify-content: center;
      transform: translateY(-40px);
      font-family: 'Nunito', sans-serif;
  }